import React, {FC} from "react";
import {
    Checkbox,
    FormControl,
    FormControlLabel,
    FormGroup,
    FormLabel,
    Radio,
    RadioGroup,
    TextField
} from "@material-ui/core";
import {TestQuestionModel} from "../constants/tests";
import LessonBlock from "./LessonBlock";

type TestQuestionProps = {
    userAnswers?: number[] | string[];
    onChangeUserAnswers?: (value: number | string) => void;
}
const TestQuestion: FC<TestQuestionModel & TestQuestionProps> = ({type, blocks, answers, userAnswers, onChangeUserAnswers}) => {

    switch (type) {
        case "single":
            return (
                <FormControl component="fieldset">
                    {!!blocks && (
                        <FormLabel component="legend">
                            {blocks.map((block, blockIdx) => (
                                <LessonBlock key={blockIdx} {...block}/>
                            ))}
                        </FormLabel>
                    )}

                    {answers && (
                        <RadioGroup
                            aria-label="answers"
                            name="answers"
                            value={(userAnswers && userAnswers[0]) || 0}
                            onChange={(e) => onChangeUserAnswers && onChangeUserAnswers(parseInt(e.target.value))}
                        >
                            {answers.map((answer, answerIdx) => (
                                <FormControlLabel key={answerIdx} value={answerIdx + 1} control={<Radio/>}
                                                  label={answer}/>
                            ))}
                        </RadioGroup>
                    )}
                </FormControl>
            );

        case "multiple":
            return (
                <FormControl component="fieldset">
                    {!!blocks && (
                        <FormLabel component="legend">
                            {blocks.map((block, blockIdx) => (
                                <LessonBlock key={blockIdx} {...block}/>
                            ))}
                        </FormLabel>
                    )}
                    {answers && (
                        <FormGroup>
                            {answers.map((answer, answerIdx) => (
                                <FormControlLabel
                                    key={answerIdx}
                                    value={answerIdx + 1}
                                    control={
                                        <Checkbox
                                            checked={userAnswers && (userAnswers as number[]).indexOf(answerIdx + 1) > -1}
                                            value={answerIdx + 1}
                                            onChange={(e) => onChangeUserAnswers && onChangeUserAnswers(parseInt(e.target.value))}
                                        />
                                    }
                                    label={answer}
                                />
                            ))}
                        </FormGroup>
                    )}
                </FormControl>
            )

        case "text":
            return (
                <div>
                    {!!blocks && (
                        <FormLabel component="legend">
                            {blocks.map((block, blockIdx) => (
                                <LessonBlock key={blockIdx} {...block}/>
                            ))}
                        </FormLabel>
                    )}
                    {answers && answers.map((answer, answerIdx) => (
                        <TextField
                            fullWidth
                            key={answerIdx}
                            value={(userAnswers && userAnswers[0]) || ''}
                            onChange={e => onChangeUserAnswers && onChangeUserAnswers(e.target.value)}
                            variant="outlined"
                        />
                    ))}
                </div>
            );

        default:
            return null;
    }
};

export default TestQuestion
