import React from "react";
import {LessonBlockModel} from "../constants/lessons";
import LessonBlock from "./LessonBlock";

const blocks: LessonBlockModel[] = [
    {
        component: 'ul',
        style: {fontSize: '20px'},
        blocks: [
            {component: 'div', style: {fontWeight: 600, marginBottom: '8px'}, body: 'Кому адресован курс:'},
            {component: 'li', body: 'Сотрудники, ответственные за обеспечение защиты персональных данных в организации'},
            {component: 'li', body: 'Всем, кто хотел бы получить базовые знания по данной теме'},
        ]
    },
    {
        component: 'ul',
        style: {fontSize: '20px'},
        body: '',
        blocks: [
            {component: 'div', style: {fontWeight: 600, marginBottom: '8px'}, body: 'Для чего:'},
            {component: 'li', body: 'Чтобы научиться защищать персональные данные сотрудников и клиентов - всю информацию, хранимую и обрабатываемую в организации. Как в бумажном, так и в электронном виде'},
        ]
    },
    {
        component: 'ul',
        style: {fontSize: '20px'},
        body: '',
        blocks: [
            {component: 'div', style: {fontWeight: 600, marginBottom: '8px'}, body: 'Чему научитесь:'},
            {component: 'li', body: 'Классифицировать и правильно ставить задачи о защите персональных данных'},
            {component: 'li', body: 'Выбирать правильные способы и инструменты (ПО) для решения конкретных задач'},
            {component: 'li', body: 'Настраивать и администрировать средства защиты информации'},
            {component: 'li', body: 'Разрабатывать, оформлять и профессионально анализировать документы по защите персональных данных'},
            {component: 'li', body: 'Полный список тем приведен в программе обучения'},
        ]
    },
    {
        component: 'p',
        style: {fontSize: '20px'},
        body: 'Разработчик:'
    },
    {
        component: 'p',
        style: {fontSize: '20px'},
        body: 'Студентка 312 группы ФКТиПМ'
    },
    {
        component: 'p',
        style: {fontSize: '20px', fontWeight: 600},
        body: 'Чирская Анна'
    },
];

const Main = () => {

    return (
        <div>
            {blocks.map((block, blockIdx) => (
                <LessonBlock key={blockIdx} {...block}/>
            ))}
        </div>
    )
};

export default Main
