import React, {FC, useCallback, useEffect, useMemo, useState} from "react";
import {Button, MobileStepper, Typography} from "@material-ui/core";
import {useParams, useHistory} from "react-router";
import tests, {TestModel} from "../constants/tests";
import TestQuestion from "./TestQuestion";
import {KeyboardArrowLeft, KeyboardArrowRight} from "@material-ui/icons";
import {Link} from "react-router-dom";

export type LocalStorageUserTest = {
    testId: number,
    activeQuestion: number,
    userAnswers: { idx: number, values: number[] | string[] }[];
}

export const activeUserTestsKey = 'activeUserTests';
export const getActiveUserTestsFromLocalStorage = (): LocalStorageUserTest[] => {
    let userTests: LocalStorageUserTest[] = []
    try {
        userTests = JSON.parse(localStorage.getItem(activeUserTestsKey) || '[]') || [];
    } catch (e) {
        console.error('error on parse userTests')
    }
    return userTests
};
export const setActiveUserTestToLocalStorage = (userTest: LocalStorageUserTest, test: TestModel = {id: 0} as TestModel) => {
    const userTests = getActiveUserTestsFromLocalStorage();

    const newUserTests = userTests.filter(t => t.testId !== test.id);
    newUserTests.push(userTest);

    localStorage.setItem(activeUserTestsKey, JSON.stringify(newUserTests))
};
export const deleteActiveUserTestToLocalStorage = (userTest: LocalStorageUserTest, test: TestModel = {id: 0} as TestModel) => {
    const userTests = getActiveUserTestsFromLocalStorage();
    const newUserTests = userTests.filter(t => t.testId !== test.id);
    localStorage.setItem(activeUserTestsKey, JSON.stringify(newUserTests))
};

export const resultsUserTestsKey = 'resultsOfUserTests';
export const getResultsOfUserTestsFromLocalStorage = (): LocalStorageUserTest[] => {
    let resultsOfTests: LocalStorageUserTest[] = [];
    try {
        resultsOfTests = JSON.parse(localStorage.getItem(resultsUserTestsKey) || '[]') || [];
    } catch (e) {
        console.error('error on parse userTests')
    }
    return resultsOfTests
};
export const setResultOfUserTestToLocalStorage = (userTest: LocalStorageUserTest, test: TestModel = {id: 0} as TestModel) => {
    const resultsOfTests = getResultsOfUserTestsFromLocalStorage();

    const newResultsOfTests = resultsOfTests.filter(t => t.testId !== test.id);
    newResultsOfTests.push(userTest);

    localStorage.setItem(resultsUserTestsKey, JSON.stringify(newResultsOfTests))
};

const getInitialStateOfUserTest = (test?: TestModel): LocalStorageUserTest => ({
    testId: (test && test.id) || 0,
    activeQuestion: 0,
    userAnswers: []
});

const Test: FC<{}> = () => {
    const {testId = ''} = useParams();
    const test = tests.find(t => t.id.toString() === testId);
    const history = useHistory();

    const maxQuestionCount = (test && test.questions && test.questions.length - 1) || 0;
    const minQuestionCount = 0;

    const [userTest, setUserTest] = useState<LocalStorageUserTest>(() => {
        const userTests = getActiveUserTestsFromLocalStorage();
        const savedCurrentUserTest = userTests.find(t => test && t.testId === test.id);
        return savedCurrentUserTest || getInitialStateOfUserTest(test)
    });

    useEffect(() => {
        test && setActiveUserTestToLocalStorage(userTest, test)
    }, [userTest, test]);

    const handleNext = () => {
        setUserTest(prevUserTest => ({...prevUserTest, activeQuestion: prevUserTest.activeQuestion + 1}));
    };
    const handleBack = () => {
        setUserTest(prevUserTest => ({...prevUserTest, activeQuestion: prevUserTest.activeQuestion - 1}));
    };
    const handleFinish = () => {
        setResultOfUserTestToLocalStorage(userTest);
        deleteActiveUserTestToLocalStorage(userTest, test);
        test && history.replace(`/results/${test.id}`);
    };

    const questionIndex = useMemo(() => {
        return test && test.questions && test.questions.findIndex((question, questionIdx) => questionIdx === userTest.activeQuestion)
    }, [userTest.activeQuestion, test]);
    const question = test && test.questions && test.questions[questionIndex || 0];

    const handleChangeUserAnswers = useCallback((value: number | string) => {
        if ((!questionIndex && questionIndex !== 0) || !question) return

        setUserTest(prevUserTest => {
            const newUserAnswer: any[] = [];
            let isNewAnswer = true;
            prevUserTest.userAnswers.forEach(answer => {
                if (answer.idx === questionIndex) {
                    isNewAnswer = false;
                    switch (question.type) {
                        case "text":
                            return newUserAnswer.push({...answer, values: [value]});
                        case "single":
                            return newUserAnswer.push({...answer, values: [value]});
                        case "multiple":
                            return newUserAnswer.push({
                                ...answer,
                                values: (answer.values as number[]).indexOf(value as number) > -1 ? (answer.values as number[]).filter(v => v !== value as number) : [...answer.values, value]
                            });
                        default:
                            return
                    }
                } else {
                    return newUserAnswer.push(answer)
                }
            });

            if (isNewAnswer) {
                newUserAnswer.push({idx: questionIndex, values: [value]})
            }

            return {...prevUserTest, userAnswers: newUserAnswer}
        })

    }, [questionIndex, question]);

    const userAnswerValues = useMemo(() => {
        const currentAnswer = userTest.userAnswers.find(answer => answer.idx === questionIndex);
        return (currentAnswer && currentAnswer.values) || []
    }, [userTest, questionIndex]);

    if (!test) {
        return (
            <Typography>
                Тест не найден
            </Typography>
        )
    }

    const isFinished = userTest.activeQuestion === maxQuestionCount;


    return (
        <>
            {test.id && <h1>{`Тест ${test.id > 1 ? test.id : ''}`}</h1>}
            {test.title && <h2>{`Тест ${test.title}`}</h2>}
            <div style={{display: 'flex', justifyContent: 'flex-end'}}>
                <Link to={`/results/${test.id}`}>Предудущие результаты</Link>
            </div>

            <div style={{minHeight: '40vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-between'}}>
                {question && (
                    <TestQuestion
                        {...question}
                        userAnswers={userAnswerValues}
                        onChangeUserAnswers={handleChangeUserAnswers}
                    />
                )}
                <MobileStepper
                    variant="progress"
                    steps={maxQuestionCount}
                    position="static"
                    activeStep={userTest.activeQuestion}
                    nextButton={
                        isFinished ? (
                            <Button size="small" onClick={handleFinish}
                                    disabled={userTest.activeQuestion !== maxQuestionCount}>
                                Завершить
                                {<KeyboardArrowRight/>}
                            </Button>
                        ) : (
                            <Button size="small" onClick={handleNext}
                                    disabled={userTest.activeQuestion === maxQuestionCount}>
                                Вперед
                                {<KeyboardArrowRight/>}
                            </Button>
                        )
                    }
                    backButton={
                        <Button size="small" onClick={handleBack}
                                disabled={userTest.activeQuestion === minQuestionCount}>
                            {<KeyboardArrowLeft/>}
                            Назад
                        </Button>
                    }
                />
            </div>
        </>
    )
};

export default Test
