import React, {FC} from "react";
import {LessonBlockModel} from '../constants/lessons';

const LessonBlock: FC<LessonBlockModel> = ({component = "div", blocks, body, ...props}) => {

    return React.createElement(
        component,
        props,
        (
            <>
                {body && body}
                {blocks && blocks.map((block, blockIdx) => (
                    <LessonBlock key={blockIdx} {...block}/>
                ))}
            </>
        )
    )
};

export default LessonBlock
