import React, {FC, Fragment, useMemo} from "react";
import {Card, CardContent, Divider, LinearProgress, Typography} from "@material-ui/core";
import {useParams} from "react-router";
import tests, {TestModel} from "../constants/tests";
import {getResultsOfUserTestsFromLocalStorage, LocalStorageUserTest} from "./Test";
import {LessonBlockModel} from "../constants/lessons";
import LessonBlock from "./LessonBlock";

type TestResultQuestionModel = {
    blocks?: LessonBlockModel,
    successAnswers?: string[] | number[];
    userAnswers?: string[] | number[];
    isSuccess?: boolean;
    isNoAnswer?: boolean;
}
type TestResultModel = {
    completeQuestionCount: number;
    failedQuestionCount: number;
    noAnswerQuestionCount: number;
    questionCount?: number;
    finishingInPercentage?: number;
    isComplete?: boolean;
    questions: TestResultQuestionModel[]
}
const getResultByTestAndUserTest = (test: TestModel, userTest: LocalStorageUserTest): TestResultModel => {
    let noAnswerQuestionCount: number = 0;
    let completeQuestionCount: number = 0;
    let failedQuestionCount: number = 0;
    let questionCount: number = (test && test.questions && test.questions.length) || 0;

    let questions: TestResultQuestionModel[] = [];
    test && test.questions && test.questions.forEach((question, questionIdx) => {
        const userAnswersInResult = userTest && userTest.userAnswers && userTest.userAnswers.filter(a => a.idx === questionIdx);

        const successAnswers: any[] = [];
        const userAnswers: any[] = [];

        question && question.answers && question.answers.forEach((answer, answerIdx: any) => {
            const isTrue = question && question.trueAnswers && (question.trueAnswers as any[]).indexOf(answerIdx + 1) > -1;
            isTrue && successAnswers.push(answer);

            if (question.type === "text") {
                const userAnswer = userAnswersInResult && userAnswersInResult.find(a => a.values);
                const userAnswerValue = (userAnswer && userAnswer.values && userAnswer.values[0]) || '';
                userAnswers.push(userAnswerValue)
            } else {
                const isUserAnswer = userAnswersInResult && userAnswersInResult.find(a => a.values && (a.values as any[]).indexOf(answerIdx + 1) > -1);
                isUserAnswer && userAnswers.push(answer)
            }

        });

        let isNoAnswer = !userAnswersInResult || !userAnswersInResult.length;
        let isSuccess = !userAnswers.filter(uA => !successAnswers.find(sA => sA === uA)).length && userAnswers.length === successAnswers.length;

        isSuccess ? (completeQuestionCount += 1) : (failedQuestionCount += 1);
        isNoAnswer && (noAnswerQuestionCount += 1);

        questions.push({
            isSuccess,
            isNoAnswer,
            successAnswers,
            userAnswers,
            blocks: question.blocks,
        })
    });

    let finishingInPercentage = (completeQuestionCount * 100 / questionCount) || 0;
    let isComplete = finishingInPercentage > 80;
    return {
        completeQuestionCount,
        failedQuestionCount,
        noAnswerQuestionCount,
        finishingInPercentage,
        questionCount,
        isComplete,
        questions,
    }
};

const TestResultQuestion = ({isSuccess, isNoAnswer, successAnswers = [], userAnswers = [], blocks = []}: TestResultQuestionModel) => (
    <div>
        <div>
            {blocks.map((block: LessonBlockModel, blockIdx: number) => (
                <LessonBlock key={blockIdx} {...block}/>
            ))}
        </div>
        <p style={{color: 'green'}}>
            {`Правыильные ответы: ${successAnswers.join(', ')}`}
        </p>
        <p style={{color: isSuccess ? 'green' : 'red'}}>
            {isNoAnswer ? '--------' : `Ваши ответы: ${userAnswers.join(', ')}`}
        </p>
    </div>
);

const TestResult = (
    {
        isComplete, finishingInPercentage, questions,
        completeQuestionCount, failedQuestionCount,
        noAnswerQuestionCount, questionCount
    }: TestResultModel) => {
    return (
        <div>
            <LinearProgress
                color={isComplete ? 'primary' : 'secondary'}
                variant="determinate"
                value={finishingInPercentage}
            />
            <div style={{padding: '4px', display: 'flex', justifyContent: 'space-between'}}>
                <h4 style={{color: 'green'}}>{completeQuestionCount}/{questionCount}</h4>
                <h4 style={{color: 'red'}}>{failedQuestionCount}/{questionCount}</h4>
                <h4 style={{color: 'gray'}}>{noAnswerQuestionCount}/{questionCount}</h4>
            </div>

            {questions && questions.map((question, questionIdx) => (
                <Fragment key={questionIdx}>
                    {!!questionIdx && <Divider/>}
                    <TestResultQuestion {...question}/>
                </Fragment>
            ))}
        </div>
    )
}

const TestResults: FC<{}> = () => {
    const {testId = ''} = useParams();
    const test = tests.find(t => t.id.toString() === testId);

    const userTests = useMemo(() => {
        const resultsOfTests = getResultsOfUserTestsFromLocalStorage();
        const resultsOfCurrentTest = resultsOfTests.filter(t => test && t.testId === test.id);
        return resultsOfCurrentTest
    }, [test]);

    const resultOfUserTest = useMemo(() => {
        return (test && userTests && userTests.map(userTest => getResultByTestAndUserTest(test, userTest))) || []
    }, [userTests, test]);

    if (!test || !resultOfUserTest || !resultOfUserTest.length) {
        return (
            <Typography>
                Результаты не найдены
            </Typography>
        )
    }

    return (
        <>
            {test.id && <h1>{`Результаты по тесту ${test.id > 1 ? test.id : ''}`}</h1>}
            {test.title && <h2>{`Тест ${test.title}`}</h2>}

            <div>
                {resultOfUserTest && resultOfUserTest.map((result, resultIdx) => (
                    <Card key={resultIdx} style={{margin: '8px 0'}}>
                        <CardContent>
                            <TestResult {...result}/>
                        </CardContent>
                    </Card>
                ))}
            </div>
        </>
    )
};

export default TestResults
