import React, {FC, HTMLAttributes} from "react";
import {
    AppBar, Badge, createStyles,
    CssBaseline,
    Divider,
    Drawer,
    IconButton,
    List,
    ListItem,
    ListItemIcon, ListItemText, makeStyles, Theme,
    Toolbar,
    Typography
} from "@material-ui/core";
import clsx from "clsx";
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import FontDownloadIcon from '@material-ui/icons/FontDownload';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import lessons from "./constants/lessons";
import tests from "./constants/tests";
import {Link, NavLink} from "react-router-dom";
import {ImportContacts} from "@material-ui/icons";

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            display: 'flex',
        },
        appBar: {
            zIndex: theme.zIndex.drawer + 1,
            transition: theme.transitions.create(['width', 'margin'], {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen,
            }),
        },
        appBarShift: {
            marginLeft: drawerWidth,
            width: `calc(100% - ${drawerWidth}px)`,
            transition: theme.transitions.create(['width', 'margin'], {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.enteringScreen,
            }),
        },
        menuButton: {
            marginRight: 36,
        },
        hide: {
            display: 'none',
        },
        drawer: {
            width: drawerWidth,
            flexShrink: 0,
            whiteSpace: 'nowrap',
        },
        drawerOpen: {
            width: drawerWidth,
            transition: theme.transitions.create('width', {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.enteringScreen,
            }),
        },
        drawerClose: {
            transition: theme.transitions.create('width', {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen,
            }),
            overflowX: 'hidden',
            width: theme.spacing(7) + 1,
            [theme.breakpoints.up('sm')]: {
                width: theme.spacing(8) + 1,
            },
        },
        toolbar: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'flex-end',
            padding: theme.spacing(0, 1),
            ...theme.mixins.toolbar,
        },
        content: {
            flexGrow: 1,
            padding: theme.spacing(3),
        },
    }),
);

const AppLayout: FC<HTMLAttributes<HTMLDivElement>> = ({children, ...props}) => {
    const classes = useStyles();

    const [open, setOpen] = React.useState(false);
    const handleDrawerOpen = () => {
        setOpen(true);
    };
    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <div {...props} className={classes.root}>
            <CssBaseline/>
            <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: open,
                })}
            >
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        className={clsx(classes.menuButton, {
                            [classes.hide]: open,
                        })}
                    >
                        <MenuIcon/>
                    </IconButton>
                    <Link style={{textDecoration: 'none', color: 'white'}} to={'/'}>
                        <Typography variant="h6" noWrap>
                            Курс по информационной безопасности
                        </Typography>
                    </Link>

                </Toolbar>
            </AppBar>
            <Drawer
                variant="permanent"
                className={clsx(classes.drawer, {
                    [classes.drawerOpen]: open,
                    [classes.drawerClose]: !open,
                })}
                classes={{
                    paper: clsx({
                        [classes.drawerOpen]: open,
                        [classes.drawerClose]: !open,
                    }),
                }}
                open={open}
            >
                <div className={classes.toolbar}>
                    <IconButton onClick={handleDrawerClose}>
                        <ChevronLeftIcon/>
                    </IconButton>
                </div>
                <Divider/>
                <List>
                    {lessons.map((lesson, index) => (
                        <NavLink
                            exact
                            to={`/lesson/${lesson.id}`}
                            key={lesson.id}
                            style={{textDecoration: 'none'}}
                            activeStyle={{
                                display: 'flex',
                                // borderLeft: '2px solid rgba(33, 69, 254, 0.8)',
                                boxSizing: 'border-box',
                                // borderRight: '2px solid rgba(33, 69, 254, 0.8)',
                                backgroundColor: 'rgba(33, 69, 254, 0.1)'
                            }}
                        >
                            <ListItem button>
                                <ListItemIcon>
                                    <Badge badgeContent={lesson.id} max={999} color="primary">
                                        <FontDownloadIcon/>
                                    </Badge>
                                </ListItemIcon>
                                <ListItemText primary={lesson.title}/>
                            </ListItem>
                        </NavLink>
                    ))}
                </List>
                <Divider/>
                <List>
                    {tests.map((test, index) => (
                        <NavLink
                            exact
                            to={`/test/${test.id}`}
                            key={test.id}
                            style={{textDecoration: 'none'}}
                            activeStyle={{
                                display: 'flex',
                                // borderLeft: '2px solid rgba(33, 69, 254, 0.8)',
                                // borderRight: '2px solid rgba(33, 69, 254, 0.8)',
                                backgroundColor: 'rgba(33, 69, 254, 0.1)'
                            }}
                        >
                            <ListItem button>
                                <ListItemIcon>
                                    <Badge badgeContent={test.id} max={999} color="primary">
                                        <AttachFileIcon/>
                                    </Badge>
                                </ListItemIcon>
                                <ListItemText primary={test.title}/>
                            </ListItem>
                        </NavLink>
                    ))}
                </List>
                <Divider/>

                <List>
                    <NavLink to={`/sources`}>
                        <ListItem button>
                            <ListItemIcon>
                                <ImportContacts/>
                            </ListItemIcon>
                            <ListItemText primary={'Список используемых источников'}/>
                        </ListItem>
                    </NavLink>
                </List>
            </Drawer>
            <main className={classes.content}>
                <div className={classes.toolbar}/>
                {children}
            </main>
        </div>
    );
};

export default AppLayout
